import uniIcon from '@/components/uni-icon/uni-icon.vue';
import { post } from '@/api/user.js'
import { api } from '@/config/common.js'
export default {
	data() {
		return {
			loading: false,
			stop: false,
			miao: 60,
			password: '',
			new_password: '',
			re_new_password: '',
			title: '修改密码'
		};
	},
	components: { uniIcon },
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		login_action(){
			uni.reLaunch({
				url:'/pages/my/login/login'
			})
		},

		handleReg() {
			const { password ,new_password,re_new_password} = this;
			if (password.length == 0) {
				this.$api.msg('原登录密码不能为空！');
				return;
			}
			if (new_password.length == 0) {
							this.$api.msg('新登录密码不能为空！');
							return;
						}
			if (re_new_password.length == 0) {
				this.$api.msg('重复密码不能为空！');
				return;
			}
			if(new_password!=re_new_password){
				this.$api.msg('两次密码不一致！');
				return;
			}
			const sendData = {
				password,
				new_password
			};
			this.loading = true;
			// uni.showLoading({
			//             title: '正在请求中'
			//         });
			
			post(api.Regist,sendData).then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
					this.login_action();
					} else {
						this.$api.msg(res.data.returnMessage) 
					}
					this.loading =false;
				}).catch(error => {
					this.loading =false;
					this.$api.msg('请求失败fail') 
				})
			
			
		},
		send() {
			const mobile = this.mobile;
			this.settime(60);
			if (mobile.length != 11) {
				this.$api.msg('手机号错误');
				return;
			}
		},
		settime(smiao) {
			const that = this;
			const miao = that.miao;
			if (miao == 0) {
				that.miao = 0;
				this.stop = false;
			} else {
				setTimeout(() => {
					smiao--;
					this.miao = smiao;
					that.settime(smiao);
				}, 1000);
			}
		}
	}
};
